function get_quest_player_info(questId, properties){
    console.log('[quest][get_quest_player_info]');

    if (questId == properties.quest_id_1) {
        return create_quest_data(properties.quest_id_1, properties.quest_id_1_step)
    }
    else if (questId == properties.quest_id_2) {
        return create_quest_data(properties.quest_id_2, properties.quest_id_2_step)
    }
    else if (questId == properties.quest_id_3) {
        return create_quest_data(properties.quest_id_3, properties.quest_id_3_step)
    }

    return undefined;
}

function create_quest_data(questId, questStep) {
    var data = [];
    data.id = questId;
    data.step = questStep;
    return data;
}

const quest = {
    get_quest_player_info
};

export default quest;