import { LOGIN, LOGOUT, LOGIN_REFRESH, MANDNDA_LOGIN } from '../actions';
import { SET_PROPERTIES, SET_STAT_PROPERTIES, SET_QUEST_PROPERTIES, 
    DELETE_QUEST_PROPERTIES, SET_SELECT_CLASS, ADD_FRIEND_PROPERTIES, DELETE_FRIEND_PROPERTIES } from '../actions';
import { SET_PAGE, SET_BACK_PAGE, SELECT_QUEST } from '../actions';
import { combineReducers } from 'redux';

const account_initial_state = {
    logged: false,
    id: '',
    name: '',
    provider: '',
    unique_id: '',
    mandnda_logged: false
};

const account = (state = account_initial_state, action) => {
    switch(action.type) {
        case LOGIN:
            window.sessionStorage.setItem('id', action.id);
            window.sessionStorage.setItem('name', action.name);
            window.sessionStorage.setItem('provider', action.provider);
            return Object.assign({}, state, {
                logged: true,
                id: action.id,
                name: action.name,
                provider: action.provider,
                unique_id: action.provider + action.id,
                mandnda_logged: false
            });
        case LOGIN_REFRESH:
            const session_id = window.sessionStorage.getItem('id');
            const session_name = window.sessionStorage.getItem('name');
            const session_provider = window.sessionStorage.getItem('provider');
            var logged_state = false;
            if (session_id !== null && session_name !== null && session_provider !== null) {
                logged_state = true;
            }
            //console.log("로그인 상태 업데이트" + logged_state)
            return Object.assign({}, state, {
                logged: logged_state,
                id: session_id,
                name: session_name,
                provider: session_provider,
                unique_id: session_provider + session_id,
                mandnda_logged: false
            });
        case LOGOUT:
            const providerName = window.sessionStorage.getItem('provider');
            window.sessionStorage.clear();
            if (providerName === 'google' )
            {
                // const auth2 = window.gapi.auth2.getAuthInstance();
                // auth2.sighOut().then(function() {
                    console.log('google logout');
                //});
            }
            else if (providerName === 'kakao' )
            {
                //window.Kakao.auth.logout()(function() {
                    console.log('kakao logout');
                //});
            }
            return Object.assign({}, state, {
                logged: false,
                id: '',
                name: '',
                provider: '',
                unique_id: '',
                mandnda_logged: false
            });
        case MANDNDA_LOGIN:
            console.log("[reducers] MANDNDA_LOGIN");
            return Object.assign({}, state, {
                mandnda_logged: true
            });
        default:
            return state;
    }
};

const properties_initial_state = {
    recv: false,
    stat_strength: '1',
    stat_wisdom: '1',
    stat_intelligence: '1',
    stat_dexterity: '1',
    quest_id_1: '',
    quest_id_1_step: '',
    quest_id_2: '',
    quest_id_2_step: '',
    quest_id_3: '',
    quest_id_3_step: '',
    class_id:'',
    friend_name_1: '',
    friend_name_2: '',
    friend_name_3: '',
};

const properties = (state = properties_initial_state, action) => {
    switch(action.type) {
        case SET_PROPERTIES:            
            console.log('SET_PROPERTIES:' + action.id);
            return Object.assign({}, state, {
                recv: true,
                stat_strength: action.data.stat_strength,
                stat_wisdom: action.data.stat_wisdom,
                stat_intelligence: action.data.stat_intelligence,
                stat_dexterity: action.data.stat_dexterity,
                quest_id_1: action.data.quest_id_1,
                quest_id_1_step: action.data.quest_id_1_step,
                quest_id_2: action.data.quest_id_2,
                quest_id_2_step: action.data.quest_id_2_step,
                quest_id_3: action.data.quest_id_3,
                quest_id_3_step: action.data.quest_id_3_step,
                friend_name_1: action.data.friend_name_1,
                friend_name_2: action.data.friend_name_2,
                friend_name_3: action.data.friend_name_3,
            });
        case SET_QUEST_PROPERTIES:
            console.log('SET_QUEST_PROPERTIES');
            var resultObject = state;
            if (action.datalist['quest_id_1_step'] != undefined) {
                resultObject = Object.assign({}, state, { 
                    quest_id_1: action.datalist['quest_id_1'],
                    quest_id_1_step: action.datalist['quest_id_1_step']
                    });
                console.log('set quest 1 :' + action.datalist['quest_id_1'] + ', step :' + action.datalist['quest_id_1_step']);
            }
            else if (action.datalist['quest_id_2_step'] != undefined) {
                resultObject = Object.assign({}, state, { 
                    quest_id_2: action.datalist['quest_id_2'],
                    quest_id_2_step: action.datalist['quest_id_2_step']
                    });
                console.log('set quest 2 :' + action.datalist['quest_id_2'] + ', step :' + action.datalist['quest_id_2_step']);
            }
            else if (action.datalist['quest_id_3_step'] != undefined) {
                resultObject = Object.assign({}, state, { 
                    quest_id_3: action.datalist['quest_id_3'],
                    quest_id_3_step: action.datalist['quest_id_3_step']
                    });
                console.log('set quest 3 :' + action.datalist['quest_id_3'] + ', step :' + action.datalist['quest_id_3_step']);
            }
            return resultObject;
        case DELETE_QUEST_PROPERTIES:
                console.log('DELETE_QUEST_PROPERTIES');
                var resultObject = state;
                if (action.datalist['quest_id_1'] != undefined) {
                    resultObject = Object.assign({}, state, { 
                        quest_id_1: undefined,
                        });
                    console.log('set quest 1 : undefined');
                }
                else if (action.datalist['quest_id_2'] != undefined) {
                    resultObject = Object.assign({}, state, { 
                        quest_id_2: undefined,
                        });
                    console.log('set quest 2 : undefined');
                }
                else if (action.datalist['quest_id_3'] != undefined) {
                    resultObject = Object.assign({}, state, { 
                        quest_id_3: undefined,
                        });
                    console.log('set quest 3 : undefined');
                }
                return resultObject;
        case ADD_FRIEND_PROPERTIES:
            console.log('ADD_FRIEND_PROPERTIES');
            var resultObject = ADD_FRIEND_PROPERTIES;
            if (action.datalist['friend_name_1'] != undefined) {
                resultObject = Object.assign({}, state, { 
                    friend_name_1: action.datalist['friend_name_1'],
                    });
                console.log('set friend 1 :' + action.datalist['friend_name_1']);
            }
            else if (action.datalist['friend_name_2'] != undefined) {
                resultObject = Object.assign({}, state, { 
                    friend_name_2: action.datalist['friend_name_2'],
                    });
                console.log('set friend 2 :' + action.datalist['friend_name_2']);
            }
            else if (action.datalist['friend_name_3'] != undefined) {
                resultObject = Object.assign({}, state, { 
                    friend_name_3: action.datalist['friend_name_3'],
                    });
                console.log('set friend 3 :' + action.datalist['friend_name_3']);
            }
            return resultObject;
        case DELETE_FRIEND_PROPERTIES:
                console.log('DELETE_FRIEND_PROPERTIES');
                var resultObject = state;
                if (action.datalist['friend_name_1'] != undefined) {
                    resultObject = Object.assign({}, state, { 
                        friend_name_1: undefined
                    });
                    console.log('set friend 1 : undefined');
                }
                else if (action.datalist['friend_name_2'] != undefined) {
                    resultObject = Object.assign({}, state, { 
                        friend_name_2: undefined
                    });
                    console.log('set friend 2 : undefined');
                }
                else if (action.datalist['friend_name_3'] != undefined) {
                    resultObject = Object.assign({}, state, { 
                        friend_name_3: undefined
                    });
                    console.log('set friend 3 : undefined');
                }
                return resultObject;
        case SET_STAT_PROPERTIES:
            return Object.assign({}, state, {
                stat_strength: action.data.stat_strength,
                stat_wisdom: action.data.stat_wisdom,
                stat_intelligence: action.data.stat_intelligence,
                stat_dexterity: action.data.stat_dexterity
            });
        case SET_SELECT_CLASS:
            console.log("new class id:");
            console.log(action.select_id);
            return Object.assign({}, state, {
                class_id: action.select_id
            });
        default:
            return state;
    }
}

const scene_initial_state = {
    current_page: 'QuestBoard',
    previous_page: 'QuestBoard'
};

const scene = (state = scene_initial_state, action) => {
    switch(action.type) {
        case SET_PAGE:
            var prev_page = state.current_page;
            console.log('set_page:' + prev_page + '=>' + action.page);
            return Object.assign({}, state, {
                current_page: action.page,
                previous_page: prev_page
            });
        case SET_BACK_PAGE:
            console.log('set_back_page:' + state.previous_page);
            return Object.assign({}, state, {
                current_page: state.previous_page
            });
        default:
            return state;
    }
}

const quest_initial_state = {
    select_questId: undefined,
    select_questData: undefined
};

const quest = (state = quest_initial_state, action) => {
    switch(action.type) {
        case SELECT_QUEST:
            console.log('select_quest:' + action.select_questId);
            console.log(action.select_questData);
            return Object.assign({}, state, {
                select_questId: action.select_questId,
                select_questData: action.select_questData
            });
        default:
            return state;
    }
}

const accountApp = combineReducers({
    account,
    properties,
    scene,
    quest
});

export default accountApp;