import React, { Component } from 'react';
import { connect } from 'react-redux';
import { set_page, select_quest } from '../actions';

import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import ListSubheader from '@material-ui/core/ListSubheader';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';

import LevelUP_MyQuest_ProgressBar from './LevelUP_MyQuest_ProgressBar'
import dotenv from 'dotenv'
//const urlMetadata = require('url-metadata')

const useStyles = makeStyles(theme => ({
  gridList_Tile_ProgressBar: {
    position: 'absolute',
    left: 30,
    width: 200,
    top: 60,
  },
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },
}));
export const LevelUP_MyQuest_Card = ({id, name, tileData, set_page, select_quest}) => {
  const classes = useStyles();

  const handleClick = (id, questNode) => {
    console.log('GridListTile select quest:' + id);
    select_quest(id, questNode);
    set_page('QuestProgress');
  }

  const onEmtpyTileClick = () => {
    console.log('onEmtpyTileClick call');
    set_page('QuestBoard');
  }

  const getRewardIcon = (type) => {
    switch (type) {
      case 'strength': return <img src={process.env.PUBLIC_URL + '/img/class_strength.png'} className="myquest_grid_card_stat_icon" />;
      case 'dexterity': return <img src={process.env.PUBLIC_URL + '/img/DexterityStat_icon.png'} className="myquest_grid_card_stat_icon" />;
      case 'intelligence': return <img src={process.env.PUBLIC_URL + '/img/class_intelligence.png'} className="myquest_grid_card_stat_icon" />;
      case 'wisdom': return <img src={process.env.PUBLIC_URL + '/img/class_wisdom.png'} className="myquest_grid_card_stat_icon" />;
    }
  }

  const getTileNode = (key, questNode) => {  
    console.log(questNode);
    
    var progressBar = undefined;
    if (undefined != questNode.list && 0 < questNode.list.length) {
      var stepPercent = parseInt(questNode.step*100/questNode.list.length);
      console.log('stepPercent:' + stepPercent);
      progressBar = <div className={classes.gridList_Tile_ProgressBar}><LevelUP_MyQuest_ProgressBar title={questNode.description} value={stepPercent} color='rgb(60,60,60)' /></div>;
    }
    return (
      <div className='myquest_grid_card' onClick={() => { handleClick(key, questNode); }}>
        <div className='myquest_grid_card_title'>
          {questNode.title}
        </div>
        <img src={process.env.PUBLIC_URL + '/img/LinkQuest_icon.png'} className='myquest_grid_card_title_link_img' />
        <span className='myquest_grid_card_title_link'>10/10</span>
        <img src={process.env.PUBLIC_URL + '/img/LinkQuest_icon.png'} className='myquest_grid_card_quest_image' />
        <span className='myquest_grid_card_quest_text'>바로 도전 가능</span>
        <div className='myquest_grid_card_stat_info'>
          {getRewardIcon(questNode.category)}
          <span className="myquest_grid_card_stat_text">{questNode.reward_exp}</span>
        </div>
      </div>

    );
  }

  const getEmptyTilenode = () => {
    return (
      <div className='myquest_grid_card' onClick={() => { onEmtpyTileClick() }}>
        <div className='myquest_grid_card_title'></div>
        <span className='myquest_grid_card_empty_text'>터치하여 새 퀘스트 찾기</span>
      </div>
    );
  }

  const getTileList = () => {
    var contents = [];
    var count = Object.keys(tileData).length;

    Object.entries(tileData).map(([key, tile]) => {
      const tileNode = getTileNode(key, tile);
      contents.push(tileNode);
    });

    for (var i = count; i < 3; i++) {
      console.log('add emtpy tile node ' + i)
      contents.push(getEmptyTilenode());
    }
    return contents;
  }

  //tile 파싱해서 타이틀, 제목, 이미지 얻어오기 
  return (
    <div className='myquest_root'>
        <div className='myquest_grid_container'>
          {getTileList()}
        </div>
    </div>
  );
}

const mapStateToProps = (state) => ({
    id: state.account.id,
    name: state.account.name
});

const mapDispatchToProps = { set_page, select_quest };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_MyQuest_Card);