import React, { Component } from 'react';
import { connect } from 'react-redux';
import { set_quest_properties, delete_quest_properties, set_page } from '../../actions';

import { makeStyles } from "@material-ui/core/styles";
import Button from '@material-ui/core/Button';
//import Snackbar from '@material-ui/core/Snackbar';
//import MuiAlert from '@material-ui/lab/Alert';
import Iframe from 'react-iframe'

import questController from '../../Controller/Quest';
import restful from './../../Controller/RestfulHandler'

// function Alert(props) {
//   return <MuiAlert elevation={6} variant="filled" {...props} />;
// }

const useStyles = makeStyles(theme => ({
  root: {
    position: "fixed",
    left: "0%",
    top: "8%",
    width: '100%',
    height: '87%',
  },
  // quest_site_link: {
  //   position: 'absolute',
  //   top: '0px',
  //   height: '100%',
  //   width: '100%',
  //   backgroundColor: 'red',
  // },
  iframe_site: {
    position: 'absolute',
    top: '0%',
    left: '0%',
    height: '100%',
    width: '100%',
    
  },
  acquire_button: {
    position: 'fixed',
    bottom: '0px',
    left: '0px',
    width: '100%',
    height: '5%'
  },
}));

export const LevelUP_Quest_Detail_View_Scene = ({ id, unique_id, name, select_questId, select_questData, player_properties, set_quest_properties, delete_quest_properties, set_page }) => {
  const classes = useStyles();
  //const [alertOpen, setAlertOpen] = React.useState(false);

  const thumbnail_url = process.env.REACT_APP_RESTFUL_SERVER_IP + 'quest/' + select_questId + '/thumbnail.jpg';
  const player_quest_properties = questController.get_quest_player_info(select_questId, player_properties);

  // const onAlertClose = (event, reason) => {
  //   if (reason === 'clickaway') {
  //     return;
  //   }
  //   setAlertOpen(false);
  // };

  const recv_acquire_quest = (response) => {
    console.log('[LevelUP_Quest_Detail_View_Scene][recv_acquire_quest]');
    console.log(response.data);
    if (response.data['success'] === 1) {
      set_quest_properties(response.data['row']);
      set_page('QuestBoard');
    }
    else {
      set_page('QuestBoard');
    }
  }

  const onAcquireQuestClick = (quest_id) => {
    console.log('onAcquireQuestClick select quest:' + quest_id);
    if (player_properties.quest_id_1 != undefined && player_properties.quest_id_2 != undefined && player_properties.quest_id_3 != undefined) {
      //setAlertOpen(true);
      set_page('QuestBoard');
      return;
    }
    restful.set_acquire_quest(unique_id, quest_id, recv_acquire_quest);
  }

  const getQuestUrl = () => {
    if (undefined == select_questData) {
      return;
    }
    return select_questData.contents;
  }

  return (
    <div className={classes.root}>
      <Button className={classes.acquire_button} variant="contained" color="primary" onClick={() => { onAcquireQuestClick(select_questId); }}>
        획 득
      </Button>
      <Iframe className={classes.iframe_site} url={getQuestUrl()} id="IFrameContents" allow="camera; microphone" frameBorder="0"/>
      {/* <Snackbar open={alertOpen} autoHideDuration={3000} onClose={onAlertClose}>
        <Alert onClose={onAlertClose} severity="success">
          Acquire quest full!
        </Alert>
      </Snackbar> */}
    </div>
  );
}

const mapStateToProps = (state) => ({
  id: state.account.id,
  unique_id: state.account.unique_id,
  name: state.account.name,
  page: state.scene.current_page,
  select_questId: state.quest.select_questId,
  select_questData: state.quest.select_questData,
  player_properties: state.properties
});

const mapDispatchToProps = { set_quest_properties, delete_quest_properties, set_page };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_Quest_Detail_View_Scene);
