import React, { Component } from 'react';
import { connect } from 'react-redux';
import { makeStyles } from "@material-ui/core/styles"
import body_img from './../../images/my_info.jpeg'
import QRCode from 'qrcode.react'
import Button from '@material-ui/core/Button'
import {EmailShareButton,FacebookShareButton,InstapaperShareButton,TelegramShareButton,TwitterShareButton} from "react-share";
import {EmailIcon,FacebookIcon,InstapaperIcon,TelegramIcon,TwitterIcon} from "react-share";

import LevelUP_ClassList from './../LevelUP_ClassList';

const useStyles = makeStyles(theme => ({
    root: {
      flexGrow: 1,
      flexDirection: 'column',
    },
    title: {
      flexGrow: 1,
    },
    body_img: {
        width: '300px',
    },
  }));

export const LevelUP_MyInfo_Scene = ({id, name}) => {
    const classes = useStyles();

    const onSearch = () => {
        console.log('onSearch');
    };

    const onRank = () => {
        console.log('onRank');
    };
    
    return (
      <div className={classes.root}>
          <QRCode value="http://www.google.com" />
          <p/>
          
          <EmailShareButton url='http://www.google.com' subject='levelup' body='levelup mail body'>
            <EmailIcon size={32} round={true} />
          </EmailShareButton>

          <InstapaperShareButton url='http://www.google.com'>
            <InstapaperIcon size={32} round={true} />
          </InstapaperShareButton>

          <FacebookShareButton url='http://www.google.com'>
            <FacebookIcon size={32} round={true} />
          </FacebookShareButton>

          <TelegramShareButton url='http://www.google.com'>
            <TelegramIcon size={32} round={true} />
          </TelegramShareButton>

          <TwitterShareButton url='http://www.google.com'>
            <TwitterIcon size={32} round={true} />
          </TwitterShareButton>

          <LevelUP_ClassList />
      </div>
    );
}

const mapStateToProps = (state) => ({
    id: state.account.id,
    name: state.account.name
});

export default connect(mapStateToProps)(LevelUP_MyInfo_Scene);
