import React, { Component } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

import LevelUP_QuestHistory_Complete from './../LevelUP_QuestHistory_Complete'
import LevelUP_QuestHistory_PvP from './../LevelUP_QuestHistory_PvP'


import restful from './../../Controller/RestfulHandler'
import dotenv from 'dotenv'
import TEXTDATA from './../../Controller/Textdata'

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <Typography
            component="div"
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && <Box p={3}>{children}</Box>}
        </Typography>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        position: "absolute",
        width: '100%',
        height: '100%',
    },
    tabBar: {
        position: "absolute",
        width: '100%',
        height: '8%',
        //backgroundColor: 'red'
    },
    contentBody: {
        position: "absolute",
        top: '8%',
        width: '100%',
        height: '90%',
        //backgroundColor: 'yellow'
    },
}));

export const LevelUP_QuestHistory_Scene = ({ id, name, player_properties }) => {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div className={classes.root}>
            <div className={classes.tabBar}>
                <AppBar position='static'>
                    <Tabs value={value} onChange={handleChange} aria-label="quest history">
                        <Tab label={TEXTDATA.MY_QUEST_HISTORY_COMPLETE_TAB_TITLE} {...a11yProps(0)} />
                        <Tab label={TEXTDATA.MY_QUEST_HISTORY_DROP_TAB_TITLE} {...a11yProps(1)} />
                        <Tab label={TEXTDATA.MY_QUEST_HISTORY_PVP_TAB_TITLE} {...a11yProps(2)} />
                        <Tab label={TEXTDATA.MY_QUEST_HISTORY_PARTY_TAB_TITLE} {...a11yProps(3)} />
                    </Tabs>
                </AppBar>
            </div>
            <div className={classes.contentBody}>
                <TabPanel value={value} index={0}>
                    <LevelUP_QuestHistory_Complete />
                </TabPanel>
                <TabPanel value={value} index={1}>
                    <span className='questhistory_content_body_text'>중단한 퀘스트가 없습니다!</span>
                </TabPanel>
                <TabPanel value={value} index={2}>
                    <LevelUP_QuestHistory_PvP />
                </TabPanel>
                <TabPanel value={value} index={3}>
                <span className='questhistory_content_body_text'>파티를 진행한 퀘스트가 없습니다!</span>
                    
                </TabPanel>
            </div>
        </div>
    );
}

const mapStateToProps = (state) => ({
    id: state.account.id,
    name: state.account.name,
    player_properties: state.properties
});

const mapDispatchToProps = { };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_QuestHistory_Scene);

