function get_level(properties){
    console.log('[player][get_level]');
    const totalExp = get_total_exp(properties);
    return totalExp/100;
}

function get_total_exp(properties) {
    const totalExp = properties.stat_strength + 
        properties.stat_wisdom + 
        properties.stat_intelligence + 
        properties.stat_dexterity;
    console.log('[player][totalExp] : ' + totalExp);
    return totalExp;
} 

function get_strength_level_from_exp(exp){
    console.log('[player][get_strength_level_from_exp] : ' + exp);
    return exp/10;
}

const player = {
    get_level,
    get_total_exp,
    get_strength_level_from_exp,
};

export default player;