import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { set_page, select_quest, delete_quest_properties } from '../../actions';

import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import restful from './../../Controller/RestfulHandler'
import dotenv from 'dotenv'

import './../Content.css'

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
  },
  gridList: {
    flexWrap: 'nowrap',
    // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
    //transform: 'translateZ(0)',
    height: 120,
  },
  gridTileListImg: {
    width: 200,
    height: 100,
  },
  title: {
    color: theme.palette.primary.light,
  },
  titleBar: {
    background:
      'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
  },
}));

var _tileData = {};

export const LevelUP_QuestBoard_Scene = ({ id, name, player_properties, set_page, select_quest }) => {
  const classes = useStyles();
  const [_recvData, setRecvData] = React.useState(false);

  useEffect(() => {
    if (false == _recvData) {
      restful.get_quest_list(recv_get_quest_list);
    }
  }, [_recvData]);

  const handleClick = (quest_id) => {
    console.log('GridListTile select quest:' + quest_id);
    set_page('QuestDetailView');
    select_quest(quest_id, _tileData[quest_id]);
  }

  const recv_get_quest_list = (response) => {
    console.log('[LevelUP_QuestBoard_Scene][recv_get_quest_list]');
    if (response.data['success'] === 1) {
      _tileData = response.data.row;
      setRecvData(true);
    }
    else {
    }
  }

  const getRewardIcon = (type) => {
    switch (type) {
      case 'strength': return <img src={process.env.PUBLIC_URL + '/img/class_strength.png'} className="questboard_grid_card_stat_icon" />;
      case 'dexterity': return <img src={process.env.PUBLIC_URL + '/img/DexterityStat_icon.png'} className="questboard_grid_card_stat_icon" />;
      case 'intelligence': return <img src={process.env.PUBLIC_URL + '/img/class_intelligence.png'} className="questboard_grid_card_stat_icon" />;
      case 'wisdom': return <img src={process.env.PUBLIC_URL + '/img/class_wisdom.png'} className="questboard_grid_card_stat_icon" />;
    }
  }

  const getTileNode = (key, tile, categoryType) => {
    var textIndex = categoryType.indexOf(tile.category);
    if (textIndex == -1) {
      return;
    }
    if (tile.visible == false) {
      return;
    }

    return (
      <div key={key} className='questboard_grid_card' onClick={() => { handleClick(key); }}>
        <img src={process.env.REACT_APP_RESTFUL_SERVER_IP + tile.img} alt={tile.title} className="questboard_grid_card_image" />
        <span className="questboard_grid_card_title">{tile.title}</span>
        <div className='questboard_grid_card_link_info'>
          <img src={process.env.PUBLIC_URL + '/img/LinkQuest_icon.png'} className="questboard_grid_card_link_icon" />
          <span className="questboard_grid_card_link_text">10/10</span>
        </div>
        <div className='questboard_grid_card_stat_info'>
          {getRewardIcon(tile.category)}
          <span className="questboard_grid_card_stat_text">{tile.reward_exp}</span>
        </div>
      </div>
    );
  }

  return (
    <div className='questboard_root'>
      <div className='questboard_grid'>
        {Object.entries(_tileData).map(([key, tile]) => {
          const tileNode = getTileNode(key, tile, "strength")
          return tileNode;
        })}
      </div>
      <div className='questboard_grid'>
        {Object.entries(_tileData).map(([key, tile]) => {
          const tileNode = getTileNode(key, tile, "dexterity")
          return tileNode;
        })}
      </div>
      <div className='questboard_grid'>
        {Object.entries(_tileData).map(([key, tile]) => {
          const tileNode = getTileNode(key, tile, "intelligence,wisdom")
          return tileNode;
        })}
      </div>
      <div className='questboard_grid'>
      </div>
    </div>
  )
}

const mapStateToProps = (state) => ({
  id: state.account.id,
  name: state.account.name,
  player_properties: state.properties
});

const mapDispatchToProps = { set_page, select_quest, delete_quest_properties };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_QuestBoard_Scene);

