import React, { Component } from 'react';
import { useDispatch, connect } from 'react-redux';
import { logout } from '../actions';

import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import AssessmentIcon from '@material-ui/icons/Assessment';
import SearchIcon from '@material-ui/icons/Search';
import TEXTDATA from './../Controller/Textdata'

import './Appbar.css';

const useStyles = makeStyles(theme => ({
  root: {
    position: "absolute",
    top: "0",
    left: "0",
    width: '100%',
    height: '100%',
    zIndex: 1,
    //backgroundColor: "red"
  },
  bar: {
    position: "absolute",
    height: "100%",
    width: "100%",
    margin: "2%",
    //backgroundColor: "blue"
  }
}));

const getTitle = (page) => {
  switch (page) {
    case 'PvP':
      return TEXTDATA.HOME_PAGE_TITLE;
    case 'my_info':
      return TEXTDATA.MY_INFO_PAGE_TITLE;
    case 'MyQuest':
      return TEXTDATA.MY_QUEST_PAGE_TITLE;
    case 'QuestBoard':
      return TEXTDATA.MY_QUEST_BOARD_PAGE_TITLE;
    case 'QuestHistory':
      return TEXTDATA.MY_QUEST_HISTORY_PAGE_TITLE;
    default:
      return '';
  }
}
export const LevelUP_AppBar = ({page}) => {
    const classes = useStyles();
    const title_text = getTitle(page);

    const onSearch = () => {
        console.log('onSearch');
    };

    const onRank = () => {
        console.log('onRank');
    };

    return (
      <div className={classes.root}>
        <div className={classes.bar}>
            <span className='appbar_title_text'>{title_text}</span>
            <img className='appbar_search_icon' onClick={onSearch} src={process.env.PUBLIC_URL + '/img/search_icon.png'} />
            <img className='appbar_rank_icon' onClick={onRank} src={process.env.PUBLIC_URL + '/img/rank_icon.png'} />
        </div>
      </div>
    );
}

const mapStateToProps = (state) => ({
    id: state.account.id,
    name: state.account.name,
    page: state.scene.current_page
});

export default connect(mapStateToProps)(LevelUP_AppBar);
