import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { add_friend_properties, delete_friend_properties } from '../../actions';

import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import ImageIcon from '@material-ui/icons/Image';
import WorkIcon from '@material-ui/icons/Work';
import BeachAccessIcon from '@material-ui/icons/BeachAccess';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import restful from './../../Controller/RestfulHandler'

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        backgroundColor: theme.palette.background.paper,
    },
}));

var _tileData = {};

export const LevelUP_Friend_Scene = ({ id, unique_id, name, player_properties, add_friend_properties, delete_friend_properties }) => {
    const classes = useStyles();
    const [openInviteDialog, setOpenInviteDialog] = React.useState(false);
    const [refreshUI, setRefreshUI] = React.useState(false);

    const recv_add_friend = (response) => {
        console.log('[LevelUP_Friend_Scene][recv_add_friend]');
        console.log(response.data);
        if (response.data['success'] === 1) {
            add_friend_properties(response.data['row']);
        }
        else {

        }
    }

    const recv_del_friend = (response) => {
        console.log('[LevelUP_Friend_Scene][recv_del_friend]');
        console.log(response.data);
        if (response.data['success'] === 1) {
            delete_friend_properties(response.data['row']);
        }
        else {

        }
    }

    const onFriendDelete = (friend_name) => {
        console.log('onFriendDelete:' + friend_name);
        restful.del_friend(unique_id, friend_name, recv_del_friend);
    };

    const onFriendInvite = () => {
        //var friend_name = this.refs.add_friend_name_ref.getValue;
        var friend_name =document.getElementById('add_friend_name_field').value;
        console.log('onFriendInvite:' + friend_name);
        restful.add_friend(unique_id, friend_name, recv_add_friend);
        onFriendInviteClose();
    }

    const onFriendInviteOpen = () => {
        setOpenInviteDialog(true);
    }

    const onFriendInviteClose = () => {
        setOpenInviteDialog(false);
    }

    const createTileData = () => {
        _tileData = {};
        console.log("[LevelUP_Friend_Scene][createTileData]");
        console.log(player_properties);
        if (undefined != player_properties.friend_name_1) { _tileData[1] = createFriendDataNode("quest/korea_history_quiz/thumbnail.jpg", player_properties.friend_name_1)}
        if (undefined != player_properties.friend_name_2) { _tileData[2] = createFriendDataNode("quest/korea_history_quiz/thumbnail.jpg", player_properties.friend_name_2)}
        if (undefined != player_properties.friend_name_3) { _tileData[3] = createFriendDataNode("quest/korea_history_quiz/thumbnail.jpg", player_properties.friend_name_3)}
        console.log(_tileData);
    }

    const createFriendDataNode = (img, name) => {
        var node = {};
        node["img"] = img;
        node["name"] = name;
        return node;
    }

    // useEffect(() => {
    //     console.log("[LevelUP_Friend_Scene][useEffect]");
    //     createTileData();
    // }, []);

    const getListItemNode = (key, node) => {
        console.log(node);
        //const labelId = `checkbox-list-secondary-label-${key}`;
        return (
            <ListItem button key={key}>
                <ListItemAvatar>
                    <Avatar alt="Remy Sharp" src={node.img} />
                </ListItemAvatar>
                <ListItemText primary={node.name} />
                <ListItemSecondaryAction>
                    <Button onClick={() => onFriendDelete(node.name)}>삭제</Button>
                </ListItemSecondaryAction>
            </ListItem>
        );
    }

    createTileData();

    return (
        <div>
        <List className={classes.root}>
            {Object.entries(_tileData).map(([key, tile]) => {
                const tileNode = getListItemNode(key, tile)
                return tileNode;
            })}
        </List>

        <Button onClick={onFriendInviteOpen} >
            친구초대
            </Button>
        <Dialog open={openInviteDialog} onClose={onFriendInviteClose} aria-labelledby="form-dialog-title">
            <DialogTitle id="form-dialog-title">친구추가</DialogTitle>
            <DialogContent>
                <TextField
                    autoFocus
                    margin="dense"
                    id="add_friend_name_field"
                    label="input id"
                    type="text"
                    fullWidth
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={onFriendInviteClose} color="primary">
                    Cancel
          </Button>
                <Button onClick={onFriendInvite} color="primary">
                    Add
          </Button>
            </DialogActions>
        </Dialog>
        </div>
    );
}

const mapStateToProps = (state) => ({
    id: state.account.id,
    unique_id: state.account.unique_id,
    name: state.account.name,
    player_properties: state.properties
});

const mapDispatchToProps = { add_friend_properties, delete_friend_properties };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_Friend_Scene);