import React from "react";
import { useDispatch, connect } from 'react-redux';
import { logout } from '../actions';

import { lighten, makeStyles, withStyles } from "@material-ui/core/styles";
import { Grid } from "@material-ui/core";
import LinearProgress from "@material-ui/core/LinearProgress";
import player from "./../Controller/Player"

function createProgressBar(bar_height, color, parent_progress) {
    const newComponent = withStyles({
      root: {
        backgroundColor: lighten(color, 0.5)
      },
      bar: {
        backgroundColor: color
      }
    })(parent_progress);
    return newComponent;
}

function newStyle(bar_height) {
  const useStyle = makeStyles(theme => ({
    root: {
      flexGrow: 1
    },
    margin: {
      margin: theme.spacing(1)
    },
    progressLabel: {
      position: "absolute",
      width: "100%",
      height: "100%",
      zIndex: 1,
      maxHeight: bar_height, // borderlinearprogress root.height
      textAlign: "center",
      display: "flex",
      alignItems: "center",
      "& span": {
        width: "100%"
      }
    },
  }));
  return useStyle;
}

export const LevelUP_MyQuest_ProgressBar = ({value, color, title}) => {
    const bar_height = 20;
    const BorderLinearProgress = withStyles({
        root: {
        height: bar_height
        },
        bar: {
        borderRadius: 30
        }
    })(LinearProgress);

    const NewProgreeBar = createProgressBar(bar_height, color, BorderLinearProgress);
    const useStyle = newStyle(bar_height);
    const classes = useStyle();
    
    function progressState(percent_value){
        //if (percent_value > 0) {
            return (
                <Grid item xs={12}>
                    <div className={classes.progressLabel}><span>{percent_value}%</span></div>
                    <NewProgreeBar variant="determinate" value={percent_value}/>
                </Grid>
            );
        //}
    }

    const progressNumber = parseInt(value);
    //const [completed, setCompleted] = React.useState(progressNumber);
    //const view = progressState(progressNumber);

    return (
        <div className={classes.root}>
        <Grid container spacing={1} justify="space-between">
            <Grid item xs={12}>
                {title}
            </Grid>
            {progressState(progressNumber)}
        </Grid>
        </div>
    );
}

export default LevelUP_MyQuest_ProgressBar;