import React, { useEffect } from 'react';
import { useDispatch, connect } from 'react-redux';
import { set_page, delete_quest_properties, set_back_page } from '../actions';

import { makeStyles } from '@material-ui/core/styles';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

import TEXTDATA from './../Controller/Textdata'
import restful from './../Controller/RestfulHandler'

import './Appbar.css'

const useStyles = makeStyles(theme => ({
  root: {
    position: "absolute",
    top: "0",
    left: "0",
    width: '100%',
    height: '100%',
    zIndex: 1,
    //backgroundColor: "red"
  },
  bar: {
    position: "absolute",
    height: "100%",
    width: "100%",
    margin: "2%",
  },
  title: {
    position: "absolute",
    zIndex: 1,
    textAlign: "left",
    margin: "2%",
    backgroundColor: "blud"
  },
}));

// ArrowBackIos
export const LevelUP_AppBar_Quest = ({unique_id, page, prev_page, select_questId, select_questData, set_page, delete_quest_properties, set_back_page }) => {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  
  useEffect(() => {
    console.log('useEffect current page:' + page + ', prev page:' + prev_page);
  });

  console.log('select quest id:' + select_questId);
  //console.log(select_questData);

  const title_text = select_questData.title;

  const recv_delete_quest = (response) => {
    console.log('[LevelUP_AppBar_Quest][recv_delete_quest]');
    console.log(response.data);
    if (response.data['success'] === 1) {
      delete_quest_properties(response.data['row']);
      set_page('MyQuest');
    }
    else {

    }
  }

  const onBack = () => {
    console.log('onBack set_page:' + prev_page);
    // if (page == 'QuestProgress') {
    //   set_page('MyQuest');
    // } else {
    //   set_page('QuestBoard');
    // }
    set_page(prev_page);
  };

  const onOpenMenu = (event) => {
    setAnchorEl(event.currentTarget);
    console.log('onOpenMenu');
  };

  const onCloseMenu = () => {
    setAnchorEl(null);
    console.log('onCloseMenu');
  };
  
  const onDropQuest = () => {
    setAnchorEl(null);
    console.log('onDropQuest select quest:' + select_questId);
    restful.delete_quest(unique_id, select_questId, recv_delete_quest);  };

  const onViewRank = () => {
    setAnchorEl(null);
    console.log('onViewRank');
  };

  return (
    <div className={classes.root}>
        <div className={classes.bar}>
            <span onClick={onBack} className='appbar_title_text'>
              <ArrowBackIosIcon />
              {title_text}
            </span>
            {page == 'QuestProgress' ? <img className='appbar_menu_icon' onClick={onOpenMenu} src={process.env.PUBLIC_URL + '/img/Extra_icon.png'} /> : undefined}
            
        </div>

        <Menu
          id="quest-quick-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={onCloseMenu}
        >
          <MenuItem onClick={onDropQuest}>퀘스트 중단</MenuItem>
          <MenuItem onClick={onViewRank}>랭킹</MenuItem>
        </Menu>
    </div>
  );
}

const mapStateToProps = (state) => ({
  id: state.account.id,
  unique_id: state.account.unique_id,
  name: state.account.name,
  page: state.scene.current_page,
  prev_page: state.scene.previous_page,
  select_questId: state.quest.select_questId,
  select_questData: state.quest.select_questData

});
const mapDispatchToProps = { set_page, delete_quest_properties, set_back_page  };
export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_AppBar_Quest);