import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { set_quest_properties, set_page } from '../../actions';

import { makeStyles } from "@material-ui/core/styles";

import Iframe from 'react-iframe'

import restful from './../../Controller/RestfulHandler'

const useStyles = makeStyles(theme => ({
  root: {
    position: "fixed",
    width: '100%',
    top: '8%',
    height: '92%'
  },
  quest_site_link: {
    position: 'absolute',
    height: '100%',
    width: '100%',
    frameBorder: 0,
  },
}));

var _enableRecvChildMessage = true;
var _active_questId = '';
export const LevelUP_MyQuest_Progress_Scene = ({ id, unique_id, name, page, select_questId, select_questData, player_properties, set_quest_properties, set_page }) => {
  const classes = useStyles();

  useEffect(() => {
    _enableRecvChildMessage = true;
    _active_questId = select_questId;
    window.removeEventListener("message", recvChildMessage); 
    window.addEventListener('message', recvChildMessage);
    console.log('useEffect call _enableRecvChildMessage:' + _enableRecvChildMessage + ', quest id:' + select_questId);
  });

  const recvChildMessage = (recv) => {
    console.log(recv["data"]);

    if (_active_questId != select_questId) {
      console.log('recvChildMessage quest id check active:' + _active_questId + ', select:'+select_questId);
      return;
    }

    if (recv["data"] == "onBack" || recv["data"] == "onFail" || recv["data"] == "onSuccess") {
      console.log('recvChildMessage call, current page:' + page + ', enable recv message:' + _enableRecvChildMessage + ', quest id:' + select_questId);
      if (_enableRecvChildMessage == false) {
        return;
      }
      _enableRecvChildMessage = false;
    }

    if (recv["data"] == "onBack" || recv["data"] == "onFail") {
      set_page('MyQuest');
    }
    else if (recv["data"] == "onSuccess") {
      restful.set_complete_quest(unique_id, select_questId, recv_complete_quest);
    }
  }

  const recv_complete_quest = (response) => {
    console.log('[LevelUP_MyQuest_Progress_Scene][recv_complete_quest]');
    console.log(response.data);
    if (response.data['success'] === 1) {
      set_quest_properties(response.data['row']);
      set_page('MyQuest');
    }
    else {

    }
  }

  const onIFrameURL = () => {
    window.addEventListener('message', recvChildMessage);
  }

  const getQuestUrl = () => {
    var quest_id = select_questId;
    if (undefined != select_questData.list && 0 < select_questData.list.length) {
      var quest_step = 0;
      if (player_properties.quest_id_1 == select_questId) {
        quest_step = player_properties.quest_id_1_step;
      }
      else if (player_properties.quest_id_2 == select_questId) {
        quest_step = player_properties.quest_id_2_step;
      }
      else if (player_properties.quest_id_3 == select_questId) {
        quest_step = player_properties.quest_id_3_step;
      }
      if (select_questData.list.length > quest_step) {
        quest_id = select_questData.list[quest_step];
      }
    }
    return process.env.REACT_APP_RESTFUL_SERVER_IP + 'quest/' + quest_id;
  }

  const iframe = <Iframe url={getQuestUrl()} className={classes.quest_site_link} id="IFrameContents"
    onLoad={onIFrameURL()} allow="camera; microphone" frameBorder="0"/>;

  return (
    <div className={classes.root}>
      {iframe}
    </div>
  );
}

const mapStateToProps = (state) => ({
  id: state.account.id,
  unique_id: state.account.unique_id,
  name: state.account.name,
  page: state.scene.current_page,
  select_questId: state.quest.select_questId,
  select_questData: state.quest.select_questData,
  player_properties: state.properties
});

const mapDispatchToProps = { set_quest_properties, set_page };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_MyQuest_Progress_Scene);
