import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { set_page, select_quest, delete_quest_properties } from './../actions';

import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import restful from './../Controller/RestfulHandler'
import dotenv from 'dotenv'

const useStyles = makeStyles(theme => ({
  root: {
    position: "absolute",
    top: "0%",
    left: "0%",
    height: "100%",
    width: "100%",
  }
}));

var _tileData = {};

export const LevelUP_QuestHistory_Complete = ({ id, name, player_properties, set_page, select_quest }) => {
  const classes = useStyles();
  const [recvData, setRecvData] = React.useState(false);

  useEffect(() => {
    restful.get_quest_list(recv_get_quest_list);
  }, []);

  const handleClick = (quest_id) => {
    console.log('GridListTile select quest:' + quest_id);
    set_page('QuestDetailView');
    select_quest(quest_id, _tileData[quest_id]);
  }

  const recv_get_quest_list = (response) => {
    console.log('[LevelUP_QuestHistory_Complete][recv_get_quest_list]');
    if (response.data['success'] === 1) {
      _tileData = response.data.row;
      setRecvData(true);
    }
    else {
    }
  }

  const getTileNode = (key, tile) => {
    return (
      <div key={key} className='questhistory_grid_card'>
        <img src={process.env.REACT_APP_RESTFUL_SERVER_IP + tile.img} className='questhistory_grid_card_img' />
        <span className='questhistory_grid_card_text'>{tile.title}</span>
      </div>
    );
    // return (
    //   <GridListTile key={key} onClick={() => { handleClick(key); }}>
    //     <img src={process.env.REACT_APP_RESTFUL_SERVER_IP + tile.img} alt={tile.title} className={classes.gridTileListImg} />
    //     <GridListTileBar
    //       title={tile.title}
    //       classes={{
    //         root: classes.titleBar,
    //         title: classes.title,
    //       }}
    //       actionIcon={
    //         <IconButton aria-label={`star ${tile.title}`}>
    //           <StarBorderIcon className={classes.title} />
    //         </IconButton>
    //       }
    //     />
    //   </GridListTile>
    // );
  }

  return (
    <div className={classes.root}>
      <div className='questhistory_grid_card_container'>
        {Object.entries(_tileData).map(([key, tile]) => {
          const tileNode = getTileNode(key, tile)
          return tileNode;
        })}
      </div>
    </div>
  );
}

const mapStateToProps = (state) => ({
    id: state.account.id,
    name: state.account.name,
    player_properties: state.properties
});

const mapDispatchToProps = { set_page, select_quest, delete_quest_properties };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_QuestHistory_Complete);

