export const LevelUP_QuestHistory_PvP = ({ id, name, select_quest }) => {
    const classes = useStyles();

    const createTileData = () => {
    }

    useEffect(() => {
        createTileData();
    }, []);

    const getListItemNode = (key, node) => {
        console.log(node);
        console.log(node["date"]);
        return (
            <ListItem button key={key}>
                <ListItemText primary={node.date} />
                <ListItemText primary={node.info} />
                <ListItemAvatar>
                    <Avatar>
                        <ImageIcon />
                    </Avatar>
                </ListItemAvatar>
                <ListItemText primary={node.rank} />
            </ListItem>
        );
    }

    return (
        <div className={classes.root}>
            <List>
                {Object.entries(_tileData).map(([key, tile]) => {
                    const tileNode = getListItemNode(key, tile)
                    return tileNode;
                })}
            </List>
        </div>
    );
}

const mapStateToProps = (state) => ({
    id: state.account.id,
    name: state.account.name
});

const mapDispatchToProps = { select_quest };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_QuestHistory_PvP);