import axios from 'axios';
import dotenv from 'dotenv'

dotenv.config();
const get_account = (id, callback) => {
    console.log('[restful][get_account] req');
    //console.log(process.env.REACT_APP_RESTFUL_SERVER_IP);
    console.log(process.env.REACT_APP_RESTFUL_SERVER_IP + 'restful/user/account/' + id);
    axios
        .get(process.env.REACT_APP_RESTFUL_SERVER_IP + 'restful/user/account/' + id)
        .then((response) => {
            console.log('[restful][get_account] recv');
            console.log(response.data);
            callback(response);
        });
}

const create_account = (id, user_name, callback) => {
    console.log('[restful][create_account] req');
    axios
        .post(process.env.REACT_APP_RESTFUL_SERVER_IP + 'restful/user/add/' + id, {
            name: user_name
        })
        .then((response) => {
            console.log('[MandndaAccount][create_account] recv');
            console.log(response.data);
            callback(response);
        });
}

