import React, { Component } from 'react';
import { connect } from 'react-redux';
import { delete_quest_properties } from '../../actions';

import { makeStyles, useTheme } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';

import LevelUP_MyQuest_List from '../LevelUP_MyQuest_List'

import restful from './../../Controller/RestfulHandler'

const useStyles = makeStyles(theme => ({
  root: {
    width: "100%",
  }
}));

var _tileData = {};

export const LevelUP_MyQuest_Scene = ({id, unique_id, name, player_properties, delete_quest_properties}) => {
  const classes = useStyles();
  const [recvData, setRecvData] = React.useState(false);

  const recv_get_my_quest_list = (response) => {
    console.log('[LevelUP_MyQuest_Scene][recv_get_my_quest_list]');
    if (response.data['success'] === 1) {
      _tileData = response.data.row;
      console.log(_tileData);
      setRecvData(true);
      check_quest_properties(_tileData);
    }
    else {
    }
  }

  const recv_delete_quest = (response) => {
    console.log('[LevelUP_MyQuest_Scene][recv_delete_quest]');
    console.log(response.data);
    if (response.data['success'] === 1) {
      delete_quest_properties(response.data['row']);
    }
    else {

    }
  }

  const check_quest_properties = (data) => {
    // id는 있지만 data가 존재하지 않는 경우 quest delete 처리
    // 서버보다 클라이언트에서 하는 것이 부하를 덜 차지 할 것 같음
    // quest data가 서버에서 삭제 되었을 경우 발생
    if (player_properties.quest_id_1 != undefined) {
      if (data[player_properties.quest_id_1] == undefined) {
        console.log('[check_quest_properties] delete quest_id_1');
        restful.delete_quest(unique_id, player_properties.quest_id_1, recv_delete_quest);
      }
    }

    if (player_properties.quest_id_2 != undefined) {
      if (data[player_properties.quest_id_2] == undefined) {
        console.log('[check_quest_properties] delete quest_id_2');
        restful.delete_quest(unique_id, player_properties.quest_id_2, recv_delete_quest);
      }
    }

    if (player_properties.quest_id_3 != undefined) {
      if (data[player_properties.quest_id_3] == undefined) {
        console.log('[check_quest_properties] delete quest_id_3');
        restful.delete_quest(unique_id, player_properties.quest_id_3, recv_delete_quest);
      }
    }
  }


  if (false == recvData) {
    restful.get_my_quest_list(unique_id, recv_get_my_quest_list);
  }

  return (
    <div className={classes.root}>
      <LevelUP_MyQuest_List tileData={_tileData}/>
    </div>
  );
}

const mapStateToProps = (state) => ({
    id: state.account.id,
    unique_id: state.account.unique_id,
    name: state.account.name,
    player_properties: state.properties
});

const mapDispatchToProps = { delete_quest_properties };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_MyQuest_Scene);

