import React, { useEffect } from 'react';
import { useDispatch, connect } from 'react-redux';
import { set_page } from '../actions';

import { lighten, makeStyles, withStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import FolderIcon from '@material-ui/icons/Folder';
import RestoreIcon from '@material-ui/icons/Restore';
import FavoriteIcon from '@material-ui/icons/Favorite';
import LocationOnIcon from '@material-ui/icons/LocationOn';

import my_info_icon_img from './../images/my_info_icon.jpg'
import my_quest_icon_img from './../images/my_quest_icon.jpg'
import quest_board_icon_img from './../images/quest_board_icon.jpg'
import home_icon_img from './../images/home_icon.jpg'

import AppBar from '@material-ui/core/AppBar';
import Grid from "@material-ui/core/Grid";
import LinearProgress from '@material-ui/core/LinearProgress';
import LevelUP_Stat_ProgressBar from './LevelUP_Stat_ProgressBar'

import "./Navigation.css";

const useStyles = makeStyles(theme => ({
    root: {
        position: "absolute",
        width: '100%',
        height: '100%',
    },
    statBar: {
        position: "absolute",
        top: "0%",
        height: "20%",
        width: '100%',
        //backgroundColor: "black"
    },
    navigationBar: {
        position: "absolute",
        top: "20%",
        height: "80%",
        width: '100%',
        backgroundColor: "#505050DD"
    },
    navigationRoot: {
        position: "relative",
        top: "20%",
        height: "80%",
        width: '100%',
        //backgroundColor: "#FF00FF"
    }
}));

export const LevelUP_NavigationBar = ({ id, name, page, set_page }) => {

    const classes = useStyles();
    const [_selectTab, setSelectTab] = React.useState(page);

    useEffect(() => {
        set_page(_selectTab);
    }, [_selectTab]);

    const getLevelIcon = () => {
        return <img className='navigationbar_class_icon' src={process.env.PUBLIC_URL + '/img/class_magic.png'} />;
    }

    const onPvPPlay = () => { setSelectTab('PvP'); }
    const onQuestBoard = () => { setSelectTab('QuestBoard'); }
    const onMyQuest = () => { setSelectTab('MyQuest'); }
    const onQuestHistory = () => { setSelectTab('QuestHistory'); }
    const onExtra = () => {

    }

    return (
        <div className={classes.root}>
            <div className={classes.statBar} >
                <LevelUP_Stat_ProgressBar color='rgb(255,0,0)' value='7' />
                <LevelUP_Stat_ProgressBar color='rgb(0,0,255)' value='8' />
            </div>
            <div>
                {getLevelIcon()}
            </div>
            <div className={classes.navigationBar}>
                <div className="navigationbar_root">
                    {_selectTab == 'PvP' ?
                        <img className='navigationbar_action_icon' src={process.env.PUBLIC_URL + '/img/MultiPlayON_icon.png'} /> :
                        <img className='navigationbar_action_icon' onClick={onPvPPlay} src={process.env.PUBLIC_URL + '/img/MultiPlayOFF_icon.png'} />}

                    {_selectTab == 'QuestBoard' ?
                        <img className='navigationbar_action_icon' src={process.env.PUBLIC_URL + '/img/QuestBoardON_icon.png'} /> :
                        <img className='navigationbar_action_icon' onClick={onQuestBoard} src={process.env.PUBLIC_URL + '/img/QuestBoardOFF_icon.png'} />}

                    {_selectTab == 'MyQuest' ?
                        <img className='navigationbar_action_icon' src={process.env.PUBLIC_URL + '/img/MyQuestON_icon.png'} /> :
                        <img className='navigationbar_action_icon' onClick={onMyQuest} src={process.env.PUBLIC_URL + '/img/MyQuestOFF_icon.png'} />}

                    {_selectTab == 'QuestHistory' ?
                        <img className='navigationbar_action_icon' src={process.env.PUBLIC_URL + '/img/PlayHistoryON_icon.png'} /> :
                        <img className='navigationbar_action_icon' onClick={onQuestHistory} src={process.env.PUBLIC_URL + '/img/PlayHistoryOFF_icon.png'} />}

                    <img className='navigationbar_action_icon' onClick={onExtra} src={process.env.PUBLIC_URL + '/img/Extra_icon.png'} />
                </div>
            </div>
        </div>
    );

}

const mapStateToProps = (state) => ({
    id: state.account.id,
    name: state.account.name,
    page: state.scene.current_page,
});

const mapDispatchToProps = { set_page };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_NavigationBar);