import React, { Component } from 'react';
import GoogleLogin from "react-google-login";
import KakaoLogin from "react-kakao-login";
import { connect } from 'react-redux';
import { login, login_refresh } from '../actions';

class Login extends Component {

    // session login check
    componentDidMount() {
        const id = window.sessionStorage.getItem('id');
        if (id !== null)
        {
            this.props.onLoginRefresh();
            console.log(id + " refresh login");
        }
    }

    // google login 
    responseGoogle = (res) => {
        if (res.googleId) {
            this.props.onLogin(res.googleId, res.profileObj.name, 'google');
            console.log(res);
        }
        else {
            console.error(res);
        }
    }

    // kakao login
    responseKakao = (res) => {
        if (res.profile.id) {
            this.props.onLogin(res.profile.id, res.profile.properties.nickname, 'kakao');
            console.log(res);
        }
        else {
            console.error(res);
        }
    }

    // guest login
    responseGuest = (res) => {
        const id = 0;
        const name = "guest";
        this.props.onLogin(id, name, 'guest');
    }


    responseFail = (err) => {
        console.erropr(err);
    }

    render() {
        return (
            <div>
                <GoogleLogin
                clientId = "524754137605-ln9lum23bio6dvlvphugj6040fet2ge5.apps.googleusercontent.com"
                buttonText="google login"
                onSuccess={this.responseGoogle}
                onFailure={this.responseFail}
                />
                <p/>
                <KakaoLogin
                    jsKey="8f8c52636783e0db088d34b55bc1099a"
                    buttonText="kakao login"
                    onSuccess={this.responseKakao}
                    onFailure={this.responseFail}
                    getProfile="true"
                />
                <button onClick={this.responseGuest}>guest</button>
            </div>
        );
    }
}

let mapDispatchToProps = (dispatch) => {
    return {
        onLogin: (id, name, provider) => dispatch(login(id, name, provider)),
        onLoginRefresh: () => dispatch(login_refresh())

    }
}

Login = connect(undefined, mapDispatchToProps)(Login);

export default Login;