export const LOGIN = 'LOGIN';
export const LOGIN_REFRESH = 'LOGIN_REFRESH';
export const LOGOUT = 'LOGOUT';
export const MANDNDA_LOGIN = 'MANDNDA_LOGIN';

export function login(user_id, user_name, user_provider) {
    return {
        type: LOGIN,
        id: user_id,
        name: user_name,
        provider: user_provider
    };
}

export function logout() {
    return {
        type: LOGOUT
    };
}

export function login_refresh() {
    return {
        type:  LOGIN_REFRESH
    };
}

export function mandnda_login() {
    return {
        type:  MANDNDA_LOGIN
    };
}

////////////////////////////////////////////////////////////////////////////////////////
export const SET_PROPERTIES = 'SET_PROPERTIES';
export const SET_STAT_PROPERTIES = 'SET_STAT_PROPERTIES';
export const SET_QUEST_PROPERTIES = 'SET_QUEST_PROPERTIES';
export const DELETE_QUEST_PROPERTIES = 'DELETE_QUEST_PROPERTIES';
export const ADD_FRIEND_PROPERTIES = 'ADD_FRIEND_PROPERTIES';
export const DELETE_FRIEND_PROPERTIES = 'DELETE_FRIEND_PROPERTIES';

export function set_properties(unique_id, user_data){
    return {
        type : SET_PROPERTIES,
        id : unique_id,
        data : user_data
    };
}
export function set_stat_properties(strength, wisdom, intelligence, dexterity){
    return {
        type : SET_STAT_PROPERTIES,
        stat_strength: strength,
        stat_wisdom: wisdom,
        stat_intelligence: intelligence,
        stat_dexterity: dexterity
    };
}

export function set_quest_properties(quest_datalist){
    return {
        type : SET_QUEST_PROPERTIES,
        datalist: quest_datalist
    };
}

export function delete_quest_properties(quest_datalist){
    return {
        type : DELETE_QUEST_PROPERTIES,
        datalist: quest_datalist
    };
}

export function add_friend_properties(friend_datalist){
    return {
        type : ADD_FRIEND_PROPERTIES,
        datalist: friend_datalist
    };
}

export function delete_friend_properties(friend_datalist){
    return {
        type : DELETE_FRIEND_PROPERTIES,
        datalist: friend_datalist
    };
}



////////////////////////////////////////////////////////////////////////////////////////
export const SET_PAGE = 'SET_PAGE';
export const SET_BACK_PAGE = 'SET_BACK_PAGE';
export const SELECT_QUEST = 'SELECT_QUEST';

export function set_page(change_page){
    return {
        type : SET_PAGE,
        page : change_page
    };
}

export function set_back_page(){
    return {
        type : SET_BACK_PAGE
    };
}
export function select_quest(questId, questData){
    return {
        type : SELECT_QUEST,
        select_questId : questId,
        select_questData : questData
    };
}

////////////////////////////////////////////////////////////////////////////////////////
export const SET_SELECT_CLASS = 'SET_SELECT_CLASS';

export function set_select_class(id){
    return {
        type : SET_SELECT_CLASS,
        select_id : id
    };
}