import React from "react";
import { useDispatch, connect } from 'react-redux';
import { logout } from '../actions';

import { lighten, makeStyles, withStyles } from "@material-ui/core/styles";
import { Grid } from "@material-ui/core";
import LinearProgress from "@material-ui/core/LinearProgress";
import player from "./../Controller/Player"

import './Navigation.css'

function createProgressBar(color, parent_progress) {
  const newComponent = withStyles({
    root: {
      backgroundColor: lighten(color, 0.5),
      height: "100%"
    },
    bar: {
      backgroundColor: color,
    }
  })(parent_progress);
  return newComponent;
}

function newStyle() {
  const useStyle = makeStyles(theme => ({
    root: {
      position: "relative",
      width: "100%",
      height: "50%"
    },
    LevelLabel: {
      position: "absolute",
      top: "-50%",
      right: "3%",
      width: "100%",
      height: "100%",
      zIndex: 1,
      textAlign: "right",
      fontSize: "70%",
      //color: "white",
    }
  }));
  return useStyle;
}

export const LevelUP_Stat_ProgressBar = ({ player_properties, logout, value, color }) => {
  const BorderLinearProgress = withStyles({
    bar: {
      //height: 200,
      borderRadius: 30
    }
  })(LinearProgress);

  const NewProgreeBar = createProgressBar(color, BorderLinearProgress);

  const useStyle = newStyle();
  const classes = useStyle();

  const progressNumber = parseInt(value);
  const [completed, setCompleted] = React.useState(progressNumber);

  //console.log('id : ' + player_properties.stat_strength);
  const strength_level = player.get_strength_level_from_exp(player_properties.stat_strength);

  return (
    <div className='navigationbar_stat_root'>
      <div className='navigationbar_stat_level_label'>
        <span>Lv.{value}</span>
      </div>
      <NewProgreeBar variant="determinate" value={progressNumber} />
    </div>
  );
}


const mapStateToProps = (state) => ({
  player_properties: state.properties
});

const mapDispatchToProps = { logout };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_Stat_ProgressBar);