import React, { Component } from 'react';
import { connect } from 'react-redux';
import { set_properties } from '../actions';
import LogoutButton from './LogoutButton.js'
import restful from './../Controller/RestfulHandler'

class LoginState extends Component {    
    constructor(props) {
        super(props);
    }

    componentWillMount() {
        restful.get_properties(this.props.unique_id, this.recv_get_properties);
    }

    recv_get_properties = (response) => {
        console.log('[LoginState][recv_get_properties]');
        if (response.data['success'] === 1) {
            //console.log(response.data.row);
            this.props.on_set_properties(this.props.unique_id, response.data.row);
        }
        else {
            
        }
    }

    render() {
        var accountState;
        var accountLogout;
        if (this.props.logged === true) {
            accountState = this.props.name + "님(" + this.props.id + ")"  + this.props.provider + "로그인";
            accountLogout = <LogoutButton/>;

            if (this.props.mandnda_logged === true) {
                accountState += 'mandnda login';
            }

            if (this.props.recv === true) {
                accountState += '=========== recv properties ===========';
            }
        }
        else {
            accountState = "로그인 필요";
        }

        return (
            <div>
                <h1>{accountState}</h1>
                <p/>
                {accountLogout}
            </div>
        );
    }
}

let mapStateToProps = (state) => {
    return {
        logged: state.account.logged,
        id: state.account.id,
        name: state.account.name,
        provider: state.account.provider,
        unique_id: state.account.unique_id,
        mandnda_logged: state.account.mandnda_logged,
        recv: state.properties.recv
    };
}

let mapDispatchToProps = (dispatch) => {
    return {
        on_set_properties: (id, data) => dispatch(set_properties(id, data))
    }
}
LoginState = connect(mapStateToProps, mapDispatchToProps)(LoginState);

export default LoginState;