import React, { Component } from 'react';
import { connect } from 'react-redux';
import Login from './Login'
import LoginState from './LoginState'
import MandndaAccount from './MandndaAccount'
import LevelUP_Main from './LevelUP_Main'

class Index extends Component {
    render() {
        var viewPage;
        if (this.props.logged === false) {
            viewPage = <Login/>;
        }
        else if (this.props.mandnda_logged === false) {
            viewPage = <MandndaAccount/>;
        }
        else if (this.props.recv === false) {
            viewPage = <LoginState/>;
        }
        else {
            viewPage = <LevelUP_Main/>;
        }
        return (viewPage);
    }
}

let mapStateToProps = (state) => {
    return {
        logged: state.account.logged,
        mandnda_logged: state.account.mandnda_logged,
        recv: state.properties.recv
    };
}

Index = connect(mapStateToProps)(Index);

export default Index;