import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { set_select_class } from './../actions';

import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';

import restful from './../Controller/RestfulHandler'
import dotenv from 'dotenv'

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }

const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        overflow: 'hidden',
        backgroundColor: theme.palette.background.paper,
    },
    gridList: {

        // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
        //transform: 'translateZ(0)',
        left: 0,
        top: 0,
    },
    gridTileListImg: {
        width: 200,
        height: 100,
    },
    title: {
        color: theme.palette.primary.light,
    },
    titleBar: {
        background:
            'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
    },
}));

var _tileData = {};

export const LevelUP_ClassList = ({ id, unique_id, name, player_properties, set_select_class }) => {
    const classes = useStyles();
    const [recvData, setRecvData] = React.useState(false);
    const [snackbarOpen, setSnackbarOpen] = React.useState(false);

    useEffect(() => {
        restful.get_class_list(recv_get_class_list);
    }, []);

    const onSnackbarOpen = () => {
        setSnackbarOpen(true);
    };

    const onSnackbarClose = (event, reason) => {
        setSnackbarOpen(false);
    };

    const onGridListClick = (class_id) => {
        console.log('GridListTile select class:' + class_id);
        restful.set_select_class(unique_id, class_id, recv_get_select_class);
    }

    const recv_get_select_class = (response) => {
        console.log('[LevelUP_ClassList][recv_get_select_class]');
        if (response.data['success'] === 1) {
            set_select_class(response.data['row']);
            setRecvData(true);
        }
        else {
            onSnackbarOpen();
        }
    }

    const recv_get_class_list = (response) => {
        console.log('[LevelUP_ClassList][recv_get_class_list]');
        if (response.data['success'] === 1) {
            _tileData = response.data.row;
            setRecvData(true);
        }
        else {
        }
    }

    const getTileNode = (key, tile) => {
        return (
            <GridListTile key={key} onClick={() => { onGridListClick(key); }}>
                <img src={process.env.REACT_APP_RESTFUL_SERVER_IP + tile.img} alt={tile.name} className={classes.gridTileListImg} />
                <GridListTileBar
                    title={tile.name}
                    classes={{
                        root: classes.titleBar,
                        title: classes.title,
                    }}
                    actionIcon={
                        <IconButton aria-label={`star ${tile.name}`}>
                            <StarBorderIcon className={classes.title} />
                        </IconButton>
                    }
                />
            </GridListTile>
        );
    }

    return (
        <div className={classes.root}>
            <GridList className={classes.gridList} cols={2.5}>
                {Object.entries(_tileData).map(([key, tile]) => {
                    const tileNode = getTileNode(key, tile)
                    return tileNode;
                })}
            </GridList>
            <Snackbar open={snackbarOpen} autoHideDuration={3000} onClose={onSnackbarClose}>
                <Alert onClose={onSnackbarClose} severity="success">
                    캐릭터 변경 실패
                </Alert>
            </Snackbar>
        </div>
    );
}

const mapStateToProps = (state) => ({
    id: state.account.id,
    unique_id: state.account.unique_id,
    name: state.account.name,
    player_properties: state.properties
});

const mapDispatchToProps = { set_select_class };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_ClassList);

