import React, { Component } from 'react';
import { connect } from 'react-redux';
import { mandnda_login } from '../actions';
import restful from './../Controller/RestfulHandler'

var LOGIN_STATE = { NONE : 0, ACCOUNT_START : 1, ACCOUNT_FAIL : 2, ACCOUNT_SUCCESS : 3, ACCOUNT_CREATE_START : 4, CREATE_FAIL : 5 } 

class MandndaAccount extends Component {  
    state = {
        mandnda_login_state: LOGIN_STATE.NONE
    }

    constructor(props) {
        super(props);
        this.onAccountCreate = this.onAccountCreate.bind(this);
      }

    componentWillMount() {
        this.accountCheck();
    }

    onAccountCreate() {
        this.accountCreate();    
    }

    recv_account_check = (response) => {
        console.log('[MandndaAccount][recv_account_check]');
        if (response.data['success'] === 1) {
            this.changeLoginState(LOGIN_STATE.ACCOUNT_SUCCESS);
            this.props.onMandnda_login();
        }
        else {
            this.changeLoginState(LOGIN_STATE.ACCOUNT_FAIL);
        }
    }

    accountCheck = () => {
        console.log('[MandndaAccount][accountCheck]unique id:' + this.props.unique_id);
        if (this.props.unique_id && this.props.unique_id.length > 0) {
            this.changeLoginState(LOGIN_STATE.ACCOUNT_START);
            restful.get_account(this.props.unique_id, this.recv_account_check);
        }
    }

    recv_create_account = (response) => {
        console.log('[MandndaAccount][recv_create_account]');
        if (response.data['success'] === 1) {
            this.accountCheck();
        }
        else {
            this.changeLoginState(LOGIN_STATE.CREATE_FAIL);
        }
    }

    accountCreate = () => {
        console.log('[MandndaAccount][accountCreate] unique id:' + this.props.unique_id);
        if (this.props.unique_id && this.props.unique_id.length > 0) {
            this.changeLoginState(LOGIN_STATE.ACCOUNT_CREATE_START);
            restful.create_account(this.props.unique_id, this.props.name, this.recv_create_account);
        }
    }

    changeLoginState(newState) {
        console.log('[MandndaAccount][changeLoginState]' + newState);
        this.setState({mandnda_login_state : newState});
    }

    render() {
        //console.log('mandnda_login_state:' + mandnda_login_state);
        var accountState;
        var accountCreate;
        switch (this.state.mandnda_login_state  ) {
            case LOGIN_STATE.NONE: {
                accountState = "대기";
            }
            break;
            case LOGIN_STATE.ACCOUNT_START: {
                accountState = "Mandnda에 로그인 중...";
            }
            break;
            case LOGIN_STATE.ACCOUNT_FAIL: {
                accountState = "Mandnda에 로그인 실패";
                accountCreate = <button onClick={this.onAccountCreate}> Mandnda에 가입</button>;
            }
            break;
            case LOGIN_STATE.ACCOUNT_SUCCESS: {
                accountState = "Mandnda에 로그인 성공";
            }
            break;
            case LOGIN_STATE.ACCOUNT_CREATE_START: {
                accountState = "Mandnda 계정 생성 중...";
            }
            case LOGIN_STATE.CREATE_FAIL: {
                accountState = "Mandnda 계정 생성 실패";
            }
        }

        return (
            <div>
                <h1>{accountState}</h1>
                <p/>
                {accountCreate}
            </div>
        );
    }
}

let mapStateToProps = (state) => {
    return {
        logged: state.account.logged,
        id: state.account.id,
        name: state.account.name,
        provider: state.account.provider,
        unique_id: state.account.unique_id,
        mandnda_logged: state.account.mandnda_logged
    };
}

let mapDispatchToProps = (dispatch) => {
    return {
        onMandnda_login: () => dispatch(mandnda_login())
    }
}
MandndaAccount = connect(mapStateToProps, mapDispatchToProps)(MandndaAccount);

export default MandndaAccount;