import React, { Component } from 'react';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';

import LevelUP_NavigationBar from './LevelUP_NavigationBar'

import LevelUP_AppBar from './LevelUP_AppBar'
import LevelUP_AppBar_Quest from './LevelUP_AppBar_Quest'

import LevelUP_MyInfo_Scene from './Scene/LevelUP_MyInfo_Scene'
import LevelUP_Home_Scene from './Scene/LevelUP_Home_Scene'
import LevelUP_MyQuest_Scene from './Scene/LevelUP_MyQuest_Scene'
import LevelUP_QuestBoard_Scene from './Scene/LevelUP_QuestBoard_Scene'
import LevelUP_MyQuest_Progress_Scene from './Scene/LevelUP_MyQuest_Progress_Scene'
import LevelUP_Quest_Detail_View_Scene from './Scene/LevelUP_Quest_Detail_View_Scene'
import LevelUP_QuestHistory_Scene from './Scene/LevelUP_QuestHistory_Scene'

import './Content.css'

const useStyles = makeStyles(theme => ({
    root: {
        position: "fixed",
        width: '100%',
        height: '100%',
    },
    app_root: {
        position: "fixed",
        left: "0%",
        top: "0%",
        width: '100%',
        height: '8%',
        //backgroundColor: "green"
    },
    body_root: {
        position: "fixed",
        left: "0%",
        top: "8%",
        width: '100%',
        height: '82%',
        //backgroundColor: "#FF000044"
    },
    navi_root: {
        position: "fixed",
        left: "0%",
        top: "88%",
        width: '100%',
        height: '12%',
    },
}));

export const LevelUP_Main = ({ page }) => {
    console.log('LevelUP_Main start');
    console.log(page);
    const classes = useStyles();


    const get_app_bar = (page) => {
        switch (page) {
            case 'PvP':
            case 'my_info':
            case 'MyQuest':
            case 'QuestBoard':
            case 'QuestHistory':
                return <LevelUP_AppBar />;
            case 'QuestDetailView':
            case 'QuestProgress':
                return <LevelUP_AppBar_Quest />;
            default:
                return '';
        }
    }

    const get_navigation_bar = (page) => {
        switch (page) {
            case 'PvP':
            case 'my_info':
            case 'MyQuest':
            case 'QuestBoard':
            case 'QuestHistory':
                return <LevelUP_NavigationBar />;
            case 'QuestDetailView':
            case 'QuestProgress':
            default:
                return '';
        }
    }

    const get_inner_page = (page) => {
        switch (page) {
            case 'PvP':
                return <LevelUP_Home_Scene />;
            case 'my_info':
                return <LevelUP_MyInfo_Scene />;
            case 'MyQuest':
                return <LevelUP_MyQuest_Scene />;
            case 'QuestBoard':
                return <LevelUP_QuestBoard_Scene />;
            case 'QuestHistory':
                return <LevelUP_QuestHistory_Scene />;
            case 'QuestDetailView':
                return <LevelUP_Quest_Detail_View_Scene />
            case 'QuestProgress':
                return <LevelUP_MyQuest_Progress_Scene />;
            default:
                return '';
        }
    }

    const showNavigationBar = () => {
        if (page == 'QuestDetailView' || page == 'QuestProgress') {
            return undefined;
        }
        return (
            <div className={classes.navi_root}>
                {get_navigation_bar(page)}
            </div>
        );
    }

    return (
        <div className={classes.root}>
            <img className='contents_background_img' src={process.env.PUBLIC_URL + '/img/background.png'} />
            <div className={classes.app_root} >
                {get_app_bar(page)}
            </div>
            <div className={classes.body_root}>
                {get_inner_page(page)}
            </div>
            {showNavigationBar()}
        </div>
    );
}

let mapStateToProps = (state) => {
    return {
        page: state.scene.current_page
    };
}

export default connect(mapStateToProps)(LevelUP_Main);