import React, { Component } from 'react';
import { connect } from 'react-redux';
import { makeStyles } from "@material-ui/core/styles"
//import LevelUP_PvP_Invite_Scene from './LevelUP_PvP_Invite_Scene'
import LevelUP_Friend_Scene from './LevelUP_Friend_Scene'

const useStyles = makeStyles(theme => ({
    root: {
      flexGrow: 1,
    },
    title: {
      flexGrow: 1,
    },
  }));

export const LevelUP_Home_Scene = ({page, name}) => {
    const classes = useStyles();
    var text = 'home page';
    return (
      <div className={classes.root}>
          {text}
          <LevelUP_Friend_Scene/>
      </div>
    );
}

const mapStateToProps = (state) => ({
    page: state.scene.current_page,
    name: state.account.name
});

export default connect(mapStateToProps)(LevelUP_Home_Scene);
